@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline; }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html {
  font-size: 16px; }

body {
  font-family: "Noto Sans", Helvetica, Arial, sans-serif;
  line-height: 1.6;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: normal; }

h1 {
  margin-bottom: 0.5rem;
  font-size: 24px;
  font-size: 1.5rem;
  font-weight: bold; }

h2 {
  margin-bottom: 1rem;
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: bold; }

h3 {
  margin-bottom: 1rem;
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: bold; }

h4 {
  margin-bottom: 1rem;
  font-size: 16px;
  font-size: 1rem;
  font-weight: bold; }

h5 {
  margin-bottom: 0.5rem;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold; }

h6 {
  margin-bottom: 0.5rem;
  font-size: 12px;
  font-size: 0.75rem;
  font-weight: bold; }

p {
  margin-bottom: 0.5rem; }

strong {
  font-weight: bold; }

em {
  font-style: italic; }

.slash {
  margin: 0 0.5rem; }

strong.label {
  margin-right: 0.5rem; }

.error-msg {
  color: #f02702; }

.loading {
  display: block;
  margin-top: 2.5rem;
  text-align: center;
  width: 100%;
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: normal; }

.container {
  margin: 0 auto;
  max-width: 60rem; }

@media screen and (min-width: 992px) {
  .main {
    background: #f1f5f8;
    background: linear-gradient(90deg, #f1f5f8 0%, #f1f5f8 24%, #fff 24%, #fff 100%); }
    .main > .container {
      display: grid;
      grid-template-columns: 1fr 3fr;
      height: calc(100vh - 256px); }
  .weather {
    margin: 0; } }

@media screen and (min-width: 1200px) {
  .main {
    background: linear-gradient(90deg, #f1f5f8 0%, #f1f5f8 26%, #fff 26%, #fff 100%); } }

.app-header {
  background: #212e39; }
  .app-header .container {
    padding: 0.5rem 1.5rem; }
  .app-header .logo {
    background: url(/static/media/logo.9195bbd4.png) center center no-repeat transparent;
    background-size: contain;
    height: 2.5rem;
    width: 8.125rem; }

.page-description {
  background: url(/static/media/plane.32899e5c.png) center center no-repeat #f1f5f8;
  background-size: cover;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5); }
  .page-description .container {
    padding: 1.5rem; }
  .page-description header {
    display: flex;
    align-items: flex-end;
    margin-bottom: 1.5rem; }
    .page-description header h1 {
      margin-bottom: 0;
      margin-right: 1.5rem; }
    .page-description header h2 {
      color: #eaeaea;
      margin-bottom: 0; }

.subheader li {
  margin-right: 1rem; }

@media screen and (min-width: 768px) {
  .page-description {
    align-items: center;
    display: flex;
    height: 12.5rem; }
    .page-description .container {
      width: 100%; }
    .page-description header {
      display: block; }
  .subheader {
    display: flex; } }

.runway-list {
  padding: 1.5rem; }
  .runway-list .card-header {
    color: #2d2d2d;
    margin-bottom: 0.5rem; }
  .runway-list .card-title,
  .runway-list .card-subtitle {
    margin-bottom: 0; }
  .runway-list .card-body {
    color: #565758; }
  .runway-list ul {
    margin-bottom: 1.5rem; }

@media screen and (min-width: 768px) {
  .runway-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 1rem;
    grid-row-gap: 1rem; }
    .runway-list ul {
      margin-bottom: 0;
      font-size: 14px;
      font-size: 0.875rem;
      font-weight: normal; } }

.weather {
  background: #f1f5f8;
  padding: 1.5rem; }
  .weather .card-header {
    color: #2d2d2d;
    margin-bottom: 0.5rem; }
  .weather .card-title,
  .weather .card-subtitle {
    margin-bottom: 0; }
  .weather .card-body {
    color: #565758; }

@media screen and (min-width: 768px) {
  .weather ul {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 1rem; } }

@media screen and (min-width: 992px) {
  .weather ul {
    display: block; } }

